import { FACEBOOK_KEYS } from '$utils/constants';

window.Webflow ||= [];
window.Webflow.push(async () => {
  // query Facebook anayltics for data from url if any
  const urlParams = new URLSearchParams(window.location.search);
  const fbclid = urlParams.get(FACEBOOK_KEYS.fbclid) || '';
  const utmSource = urlParams.get(FACEBOOK_KEYS.utmSource) || '';
  const utmMedium = urlParams.get(FACEBOOK_KEYS.utmMedium) || '';
  const utmCampaign = urlParams.get(FACEBOOK_KEYS.utmCampaign) || '';

  // set the data to local storage
  if (fbclid) localStorage.setItem(FACEBOOK_KEYS.fbclid, fbclid);
  if (utmSource) localStorage.setItem(FACEBOOK_KEYS.utmSource, utmSource);
  if (utmMedium) localStorage.setItem(FACEBOOK_KEYS.utmMedium, utmMedium);
  if (utmCampaign) localStorage.setItem(FACEBOOK_KEYS.utmCampaign, utmCampaign);
});
